<template>
    <div class="container">
        <!---->
        <div v-if="isSkeletonLoading" class="vue-skeleton-loading">
            <div class="skeleton-bac-animation"></div>
            <div>
                <row padding="2px 20px 2px">
                    <skeleton-square
                            width="355px"
                            height="20px"
                    ></skeleton-square>

                </row>

                <row slot="list" padding="15px 20px 0">

                    <skeleton-square
                            width="50px"
                            height="50px"
                    >
                    </skeleton-square>

                    <column>
                        <skeleton-square
                                width="100px"
                                :count="1"
                                margin="5px 10px 5px"
                                height="20px"
                        ></skeleton-square>
                        <skeleton-square
                                width="140px"
                                :count="1"
                                margin="5px 10px 5px"
                                height="20px"
                        ></skeleton-square>
                    </column>

                </row>

                <row slot="list" padding="15px 10px 0">


                    <skeleton-square
                            width="350px"
                            :count="1"
                            margin="5px 10px 5px"
                            height="100px"
                    ></skeleton-square>

                </row>

                <row slot="list" padding="2px 10px 0">


                    <skeleton-square
                            width="165px"
                            :count="1"
                            margin="5px 10px 5px"
                            height="20px"
                    ></skeleton-square>
                    <skeleton-square
                            width="160px"
                            :count="1"
                            margin="5px 10px 5px"
                            height="20px"
                    ></skeleton-square>
                </row>

                <row slot="list" padding="15px 20px 0">

                    <skeleton-square
                            width="50px"
                            height="50px"
                    >
                    </skeleton-square>

                    <column>
                        <skeleton-square
                                width="100px"
                                :count="1"
                                margin="5px 10px 5px"
                                height="20px"
                        ></skeleton-square>
                        <skeleton-square
                                width="140px"
                                :count="1"
                                margin="5px 10px 5px"
                                height="20px"
                        ></skeleton-square>
                    </column>

                </row>

                <row slot="list" padding="15px 10px 0">


                    <skeleton-square
                            width="350px"
                            :count="1"
                            margin="5px 10px 5px"
                            height="100px"
                    ></skeleton-square>

                </row>

                <row slot="list" padding="2px 10px 0">


                    <skeleton-square
                            width="165px"
                            :count="1"
                            margin="5px 10px 5px"
                            height="20px"
                    ></skeleton-square>
                    <skeleton-square
                            width="160px"
                            :count="1"
                            margin="5px 10px 5px"
                            height="20px"
                    ></skeleton-square>
                </row>

                <row slot="list" padding="15px 20px 0">

                    <skeleton-square
                            width="50px"
                            height="50px"
                    >
                    </skeleton-square>

                    <column>
                        <skeleton-square
                                width="100px"
                                :count="1"
                                margin="5px 10px 5px"
                                height="20px"
                        ></skeleton-square>
                        <skeleton-square
                                width="140px"
                                :count="1"
                                margin="5px 10px 5px"
                                height="20px"
                        ></skeleton-square>
                    </column>

                </row>

                <row slot="list" padding="15px 10px 0">


                    <skeleton-square
                            width="350px"
                            :count="1"
                            margin="5px 10px 5px"
                            height="100px"
                    ></skeleton-square>

                </row>

                <row slot="list" padding="2px 10px 0">


                    <skeleton-square
                            width="165px"
                            :count="1"
                            margin="5px 10px 5px"
                            height="20px"
                    ></skeleton-square>
                    <skeleton-square
                            width="160px"
                            :count="1"
                            margin="5px 10px 5px"
                            height="20px"
                    ></skeleton-square>
                </row>
            </div>
        </div>

        <template v-else>
            <!---->
            <nut-scroller class="tagScroll"
                          :scrollTo="tagScrollY"
                          @scrollToCbk="tagScrollToCbk"
            >

                <div slot="list" class="tagItem nut-hor-list-item " :class="{tagCurrent: currentTag==-1}"
                     @click="changeTag(-1,'recommend')"
                >
                    <span class="tagTitle"></span>
                </div>

                <div slot="list" class="tagItem nut-hor-list-item "
                     v-if="$store.state.userInfo"
                     :class="{tagCurrent: currentTag==-2}"
                     @click="changeTag(-2,'follow')"
                >
                    <span class="tagTitle"></span>
                </div>

                <div slot="list" class="tagItem nut-hor-list-item "
                     :ref="'tagScroll'+index"
                     :class="{tagCurrent: currentTag==index}"
                     v-for="(item,index) in categoryList" :key="item.id"
                     @click="changeTag(index,item.id)"
                >
                    <span class="tagTitle">{{item.topicName}}</span>
                </div>

            </nut-scroller>

            <div class="line"></div>
            <nut-scroller
                    ref="mynutscroller"
                    :scrollTo="momentListScrollY"
                    type="vertical"
                    class="totalScroll"
                    @loadMore="loadMoreVert"
                    @pulldown="pulldown"
                    @scrollChange="scrollChange"
                    :is-un-more="isUnMore"
                    :is-loading="isLoading"
                    @scrollToCbk="scrollToCbk"
                    unloadMoreTxt=""
            >
                <div slot="list" class="moment-list">

                    <div v-for="item in momentList" :key="item.id">

                        <li class="moment-item">

                            <div class="header">
                                <div class="avatar">

                                    <img :src="item.avatar" alt="">
                                </div>
                                <div class="userinfo">
                                    <div class="nickname">{{item.nickname}}</div>
                                    <div class="desc">
                                        <div class="post">{{item.company?item.company:''}}
                                            @{{item.position?item.position:''}}
                                        </div>
                                        <div class="dot"></div>
                                        <div class="publish-time">{{item.createTime}}</div>
                                    </div>
                                </div>

                            </div>
                            <div class="middle">

                                <!---->
                                <div class="content" v-html="item.content">

                                </div>

                                <!---->
                                <div class="img-list" v-if="item.images.length>0">


                                    <div
                                            @click="showBigImg(item.images,index)"
                                            v-for="(img,index) in item.images" :key="img"
                                            class="bg-img"
                                            v-bind:style="{backgroundImage:`url('${img}')`}">
                                    </div>

                                </div>

                                <!---->
                                <div class="link-box" v-if="item.linkUrl">
                                    <a @click="toArticlePage(item.linkUrl)">

                                        <!---->
                                        <div class="link-cover"
                                             v-if="item.linkCover"
                                             v-bind:style="{backgroundImage:`url('${item.linkCover})`}"
                                        >
                                        </div>

                                        <!---->
                                        <div class="link-cover-default"
                                             v-else
                                        >
                                            <i class="iconfont icon-link"></i>
                                        </div>

                                        <div class="link-info">
                                            <div class="link-title">{{item.linkTitle?item.linkTitle:'-'}}</div>
                                            <div class="link-domain">{{item.linkHost}}</div>
                                        </div>
                                    </a>
                                </div>


                                <div class="topic" v-if="item.topicName" @click="clickToTopic(item.topicId)">
                                    <i class="iconfont icon-topic">{{item.topicName}}</i>
                                </div>

                            </div>
                            <div class="action">

                                <i class="action-btn iconfont icon-dianzan"
                                   :class="{active:item.thumbUpActive}"
                                   @click="thumbUpMoment(item)"
                                >
                                    <span style="padding: 0px 5px">{{item.thumbUpCount}}</span></i>

                                <i class="action-btn iconfont icon-pinglunxiao" @click="openCommontList(item)">
                                    <span style="padding: 0px 5px">{{item.commentCount}}</span>
                                </i>
                                <i class="action-btn iconfont icon-fenxiang1"></i>

                            </div>
                            <!---->
                            <div v-if="item.showCommont" class="reviewList" >
                                <div class="commentInput"  >
                                    <!---->
                                    <nut-textbox
                                            class="inputBox"
                                            v-model="item.commentStr"
                                            :max-num="300"
                                            :txt-area-h="80"
                                    ></nut-textbox>
                                    <button  @click="sendMoyuComment(item)" class="submit-btn"></button>
                                </div>
                                <div class="reviewItem" v-for="currentComment in item.commentList" :key="currentComment.id">
                                    <!---->
                                    <div class="left">
                                        <img :src="currentComment.avatar">
                                    </div>

                                    <div class="reviewRight">
                                        <!---->
                                        <div class="top">
                                            <span class="reviewNikename">{{currentComment.nickname}}</span>
                                            <span >{{currentComment.createTime}}</span>
                                        </div>
                                        <!---->
                                        <div class="reviewBottom">{{currentComment.content}}</div>

                                        <!---->
                                        <div class="comment-btn">
                                            <span @click="openCommentPanel(currentComment,4,currentComment.userId,currentComment.nickname,item)"></span>
                                        </div>

                                        <!---->
                                        <!---->
                                        <div class="subcommentBox" v-if="currentComment.subComments.length===1">
                                            <div class="subcomment">
                                                <!---->
                                                <div class="subleft">
                                                    <img :src="currentComment.subComments[0].avatar">
                                                </div>

                                                <div class="subreviewRight">
                                                    <!---->
                                                    <div class="subtop">
                                                        <span class="subreviewNikename">{{currentComment.subComments[0].nickname}}</span>
                                                        <span >{{currentComment.subComments[0].createTime}}</span>
                                                    </div>
                                                    <!---->
                                                    <p class="subreviewBottom">
                                                        {{currentComment.subComments[0].nickname? ' '+currentComment.nickname+':':'' }}
                                                        {{currentComment.subComments[0].content}}
                                                    </p>
                                                    <!---->
                                                    <div class="comment-btn">
                                                        <!--todo-->
                                                        <span @click="openCommentPanel(currentComment,3,
                                                        currentComment.subComments[0].userId,
                                                         currentComment.subComments[0].nickname,
                                                         item
                                                        )"></span>
                                                    </div>
                                                </div>
                                            </div>

                                        </div>

                                        <!---->
                                        <div class="subcommentBox" v-if="currentComment.subComments.length>1 && !currentComment.showMore">
                                            <div class="subcomment">
                                                <!---->
                                                <div class="subleft">
                                                    <img :src="currentComment.subComments[0].avatar">
                                                </div>

                                                <div class="subreviewRight">
                                                    <!---->
                                                    <div class="subtop">
                                                        <span class="subreviewNikename">{{currentComment.subComments[0].nickname}}</span>
                                                        <span >{{currentComment.subComments[0].createTime}}</span>
                                                    </div>
                                                    <!---->
                                                    <p class="subreviewBottom">
                                                        {{currentComment.subComments[0].targetUserNickname? ' '+currentComment.subComments[0].targetUserNickname+':':'' }}
                                                        {{currentComment.subComments[0].content}}
                                                    </p>
                                                    <!---->
                                                    <div class="comment-btn">
                                                        <!--todo-->
                                                        <span @click="openCommentPanel(currentComment,
                                                        2,currentComment.subComments[0].userId,currentComment.subComments[0].nickname,
                                                        item

                                                        )"></span>

                                                    </div>
                                                </div>
                                            </div>
                                            <!--showMore-->
                                            <span class="sub-see-more" @click="currentComment.showMore=true">({{currentComment.subComments.length-1}})</span>
                                        </div>

                                        <!---->
                                        <div class="subcommentBox" v-if="currentComment.subComments.length>1 && currentComment.showMore">
                                            <div class="subcomment" v-for="subitem in currentComment.subComments" :key="subitem.id">
                                                <!---->
                                                <div class="subleft">
                                                    <img :src="subitem.avatar">
                                                </div>

                                                <div class="subreviewRight">
                                                    <!---->
                                                    <div class="subtop">
                                                        <span class="subreviewNikename">{{subitem.nickname}}</span>
                                                        <span >{{subitem.createTime}}</span>
                                                    </div>
                                                    <!---->
                                                    <p class="subreviewBottom">
                                                        {{subitem.targetUserNickname? ' '+subitem.targetUserNickname+':':'' }}
                                                        {{subitem.content}}
                                                    </p>
                                                    <!---->
                                                    <div class="comment-btn">
                                                        <!--todo-->
                                                        <span @click="openCommentPanel(currentComment,1,subitem.userId,subitem.nickname,item)"></span>
                                                    </div>
                                                </div>


                                            </div>
                                            <!--showMore-->
                                            <span class="sub-see-more" @click="currentComment.showMore=false"></span>

                                        </div>
                                    </div>
                                </div>

                                <div class="reviewItem" v-show="item.commentList&&item.commentList.length==0">
                                    <div class="noComment ">
                                        <i class="iconfont icon-meiyoushuju"></i>  ~~
                                    </div>
                                </div>
                            </div>


                        </li>
                        <div class="line"></div>
                    </div>

                </div>
            </nut-scroller>

            <!---->
            <div class="moyu-send-btn" @click="openMoyuPanel">
                <i class="action-btn iconfont icon-jiahao" ></i>
            </div>
            <!----->
            <nut-popup
                    position="bottom"
                    v-model="showCommentPanel"
            >

                <!---->
                <nut-textbox
                        class="comment-input"
                        v-model="popCommentStr"
                        :place-text="commentPlaceText"
                        :max-num="300"
                        :txt-area-h="80"
                ></nut-textbox>
                <nut-button
                        type="light"
                        @click="handlePopSubmit"
                        block
                >
                    
                </nut-button>

            </nut-popup>
        </template>
    </div>
</template>

<script>
    import api from '../../api/index'
    import {parseDomain, fromUrl} from "parse-domain";
    import Vue from 'vue'
    import {mapState} from "vuex";
    export default {
        name: "Moment",
        data() {
            return {
                isSkeletonLoading: true,//
                isUnMore: false,//
                isLoading: false,//
                currentTag: -1,//tag
                categoryList: [], //
                currentTagId: 'recommend',//tagid
                currentPage: 1, //
                momentListScrollY: 0, //
                tagScrollY: 0, //
                currentScroll: 0,//
                scrollElem: {},//
                imgList: [],//emoji
                momentList: [],//
                toast: {},//
                scrollTo: 0,//
                commentPlaceText:'',//
                showCommentPanel:false,//
                popCommentStr:'',//
                popCommentType:0,//
                popCommentObj:{},//
                popCommentTargetUserId:0,//id
                commentMomentItem:{},//
            }
        },
        mounted() {
            //
            this.isSkeletonLoading = true
            this.getMoYuList();

            this.getMomentTopicList()
        },
        computed:{
            ...mapState(['userInfo']),
        },
        methods: {


            /**
             * 
             * 
             * 
             */
            tagScrollToCbk() {
                console.log("tagScrollToCbk")
            },

            /**
             * 
             */
            scrollToCbk() {
                console.log("scrollToCbk")
            },

            /**
             * 
             */
            async getMomentTopicList() {
                let allTopicResult = await api.getMoyuAllTopic()
                let hotTopicResult = await api.getMoyuHotTopic()

                let allTopicList = allTopicResult.data

                //allTopicListhotTopicList
                //hotTopicListallTopicList
                let hotTopicIds = hotTopicResult.data.map(currentValue => currentValue.id)


                //allTopicListhotTopic
                let hotInAll = []

                //hotTopictopic
                let hotNotInAll = []

                //
                allTopicList.forEach(item => {
                    if (hotTopicIds.includes(item.id)) {
                        hotInAll.push(item)
                    } else {
                        hotNotInAll.push(item)
                    }
                })

                //hotInAllhotNotInAll
                this.categoryList = hotInAll.concat(hotNotInAll)


            },

            /**
             * 
             * @param topicId  Idrecommend follow
             * @param page     1
             * @param isMerge  
             */
            async getMoYuList(topicId = 'recommend', page = 1, isMerge = false) {


                let result = await api.getMoyuList(topicId, page)
                let sourceList = result.data.list

                //
                for (let i = 0; i < sourceList.length; i++) {


                    let item = sourceList[i]

                    //thumbUpCount
                    //thumbUpCount
                    item.thumbUpCount = item.thumbUpList.length

                    //
                    if (item.userId === '1256120724666454016') {
                        sourceList.splice(i, 1)
                        continue
                    }

                    //
                    item.thumbUpActive = this.idIsContainMe(item.thumbUpList)

                    //
                    item.createTime = this.calcTime(item.createTime)

                    //
                    if (item.linkUrl) {
                        item.linkHost = this.getDomain(item.linkUrl)
                    }
                }

                if (isMerge) {
                    this.momentList = this.mergeTargetFromSource(this.momentList, sourceList, 'id')
                } else {
                    this.momentList = sourceList
                }

                //
                this.isSkeletonLoading = false

                this.isUnMore = false
                this.isLoading = false
                if (this.toast.hide) {

                    this.$nextTick(() => {
                        this.toast.hide();
                    })
                }

            },


            /**
             * 
             */
            loadMoreVert() {
                this.isUnMore = true
                this.getMoYuList(this.currentTagId, ++this.currentPage, true)

                this.toast = this.$toast.loading('...', {
                    duration: 0,
                    id: 'momentLoading'
                });

            },

            /**
             * 
             */
            pulldown() {
                this.isLoading = true
                this.getMoYuList(this.currentTagId, 1, false)

            },

            /**
             * 
             */
            scrollChange(scrollY) {

                this.currentScroll = scrollY
                if ((-scrollY) > 200) {
                    this.isShowBacktop = true
                } else {
                    this.isShowBacktop = false

                }
            },


            /**
             * 
             * @param tagIndex
             */
            changeTag(tagIndex, tagId) {
                this.currentTag = tagIndex
                this.currentTagId = tagId
                this.getMoYuList(this.currentTagId, 1, false)
            },

            /**
             * idid
             * @param userIdList
             */
            idIsContainMe(userIdList) {

                if (!this.$store.state.userInfo) {
                    return false
                }

                if (userIdList.includes(this.$store.state.userInfo.id)) {
                    return true
                }
                return false
            },

            /**
             * url
             * @param url
             */
            getDomain(url) {
                let parseResult = parseDomain(fromUrl(url));
                return parseResult.hostname
            },

            /**
             * 
             * @param images url
             * @param index  
             *
             */
            showBigImg(images, index) {
                this.$imageTouch(
                    {
                        imageList: images, // 
                        activedIndex: index, // 
                        isShowBar: true,   // 
                    }
                );
            },


            /**
             * 
             * @param topicId
             */
            clickToTopic(topicId) {

                //

                let topicIndex = 0
                let topicItem = this.categoryList.filter((value, index) => {
                    if (value.id === topicId) {

                        topicIndex = index
                        return true
                    } else {
                        return false
                    }
                })

                if (topicItem.length > 0) {

                    //tag
                    let clickTagItem = this.$refs['tagScroll' + topicIndex][0]

                    //
                    clickTagItem.click()

                    //tag
                    // +40
                    this.tagScrollY = -clickTagItem.getBoundingClientRect().left + 40

                    //
                    if (this.momentListScrollY === -1) {
                        this.momentListScrollY = this.momentListScrollY + 0.1
                    } else {
                        this.momentListScrollY = -1
                    }

                }

            },


            /**
             * 
             * @param momentId
             */
            async thumbUpMoment(moment) {
                //sobbug
                api.momentThumbUp(moment.id)
                api.momentThumbUp(moment.id)

                //
                moment.thumbUpCount++

                //flag
                moment.thumbUpActive=true
            },

            /**
             * 
             * @param momentItem
             * @returns {Promise<void>}
             */
            async getMomentCommontList(momentItem){
                let result = await api.getMoyuCommontList(momentItem.id)
                //
                for (let i = 0; i < result.data.list.length; i++) {
                    //showMore
                    result.data.list[i].showMore = false;
                }
                Vue.set(momentItem,'commentList', result.data.list)
            },

            /**
             * 
             * @param momentItem
             */
            openCommontList(momentItem){
                if (momentItem.showCommont == true){
                    //vue
                    Vue.set(momentItem,'showCommont',false)
                    momentItem.commentStr =''
                }else {
                    Vue.set(momentItem,'showCommont',true)
                    this.getMomentCommontList(momentItem);
                }
            },

            /**
             * 
             * @param url
             */
            toArticlePage(url){
                //
                //https://www.sunofbeach.net/a/1580461941464240130
                if (url.includes("www.sunofbeach.net/a")){
                    let articleId = url.substr(url.indexOf("/a/")+3,url.length-1)
                    this.$router.push({
                        path: '/detail',
                        query: {id: articleId}
                    })
                }else {
                    console.log("")
                    window.location.href=url
                }
            },


            /**
             * 
             * @param commentStr
             */
            async sendMoyuComment(momentItem) {
                console.log("")
                console.log(momentItem)
                let result = await api.sendMoyuComment(momentItem.id, momentItem.commentStr);
                if (result.success) {
                    this.$toast.success('');
                    momentItem.commentStr = ''
                    //
                    this.getMomentCommontList(momentItem);
                } else {
                    this.$toast.fail('' + result.message);
                }
            },

            /**
             * 
             */
            openCommentPanel(commentObj,type,targetUserId,targetUserName,momentItem){
                this.commentPlaceText = ''
                //
                this.popCommentStr = ""
                //
                this.showCommentPanel = true
                this.popCommentTargetUserId = targetUserId
                this.commentMomentItem = momentItem
                this.popCommentObj = commentObj;
                if (!this.isLogin()) {
                    return
                }

                /**
                 * 4 
                 *
                 */
                this.popCommentType = type
                this.commentPlaceText=` @${targetUserName}`
            },
            /**
             * 
             */
           async sendMoyuComent(){
                let result = await api.sendMoyuSubComment(
                    this.popCommentStr,
                    this.popCommentObj.momentId,
                    this.popCommentTargetUserId,
                    this.popCommentObj.id
                )

                if (result.code === 10000) {
                    this.$notify.success(result.message)

                    //
                    this.getMomentCommontList(this.commentMomentItem);
                } else {
                    this.$notify.warn(result.message)
                }
            },

            /**
             * 
             */
            handlePopSubmit() {
                if (this.popCommentType===5){
                    //
                    this.sendMoyu();
                }else {
                    this.sendMoyuComent();
                }

                //
                this.popCommentStr = ''
                //
                this.showCommentPanel = false
                this.popCommentObj={}

            },


            /**
             * 
             */
            openMoyuPanel() {
                this.commentPlaceText = ''
                this.popCommentType = 5
                //
                this.popCommentStr = ""
                //
                this.showCommentPanel = true
                if (!this.isLogin()) {
                    return
                }
            },
            /**
             * 
             */
           async sendMoyu(){
                let result = await api.publishMoyu(this.popCommentStr ,'',[])
                console.log(result)
                if (result.code === 10000) {
                    this.$notify.success(result.message)
                    //
                    this.pulldown();
                } else {
                    this.$notify.warn(result.message)
                }
            }
        }

    }
</script>

<style scoped>
    .totalScroll {
        height: calc(100vh - 59px);
    }

    /*--------------- ---------------*/
    .tagScroll {
        white-space: nowrap;
        height: 40px;
    }

    .tagItem {
        box-sizing: border-box;

