<template>

    <nut-tab :is-scroll="true" position-nav="left" :wrapper-height="tabHeight" :def-index="currentTab"
             @tab-switch="tabSwitch"
    >
        <nut-tab-panel tab-title="">
            <nut-scroller
                    :type="'vertical'"
                    :is-un-more="isUnMore"
                    :is-loading="isLoading"
                    @pulldown="pulldown"
                    unloadMoreTxt=""
                    @loadMore="loadMoreVert"
                    :init-data="msgList"

            >
                <div slot="list">
                    <li class="mli" v-for="comment in msgList" :key="comment._id">

                        <div class="mItem">
                            <div class="mleft">

                                <img :src="comment.avatar"
                                     alt="">

                            </div>

                            <div class="mRight">
                                <div class="nickname">{{comment.nickname}}</div>
                                <div class="content">{{comment.content}}</div>
                                <div class="title"></div>
                            </div>
                        </div>


                        <div class="mBottom">
                            <span class="readStatus" :class="comment.hasRead==1?'read':''">{{comment.hasRead==1?'':''}}</span>
                            <div class="mTime">{{comment.publishTime}}</div>
                        </div>
                    </li>
                </div>
            </nut-scroller>
        </nut-tab-panel>

        <nut-tab-panel tab-title="">

            <nut-scroller
                    :type="'vertical'"
                    :is-un-more="isUnMore"
                    :is-loading="isLoading"
                    @pulldown="pulldown"
                    unloadMoreTxt=""
                    @loadMore="loadMoreVert"
                    :init-data="msgList"

            >

                <div slot="list">
                    <li class="mli" v-for="thumbItem in msgList" :key="thumbItem._id">

                        <div class="mItem">
                            <div class="mleft">

                                <img :src="thumbItem.avatar"
                                     alt="">

                            </div>

                            <div class="mRight">
                                <div class="nickname">{{thumbItem.nickname}}</div>
                                <span class="thumbUpInfo"></span>
                                <div class="title">{{thumbItem.title}}</div>


                            </div>
                        </div>

                        <div class="mBottom">
                            <span class="readStatus" :class="thumbItem.hasRead==1?'read':''">{{thumbItem.hasRead==1?'':''}}</span>
                            <div class="mTime">{{thumbItem.thumbTime}}</div>
                        </div>

                    </li>

                </div>
            </nut-scroller>

        </nut-tab-panel>

        <nut-tab-panel tab-title="">

            <nut-scroller
                    type="vertical"
                    :is-un-more="isUnMore"
                    :is-loading="isLoading"
                    @pulldown="pulldown"
                    unloadMoreTxt=""
                    @loadMore="loadMoreVert"
                    :init-data="msgList"

            >

                <div slot="list">
                    <li class="mli" v-for="article in msgList" :key="article._id">

                        <div class="mItem">
                            <div class="mleft">

                                <img :src="article.avatar"
                                     alt="">

                            </div>

                            <div class="mRight">
                                <div class="nickname">{{article.nickname}}</div>
                                <div class="content">{{article.content}}</div>
                                <div class="title">{{article.title}}</div>
                            </div>
                        </div>

                        <div class="mBottom">
                            <span class="readStatus" :class="article.hasRead==1?'read':''">{{article.hasRead==1?'':''}}</span>
                            <div class="mTime">{{article.createTime}}</div>
                        </div>
                    </li>
                </div>
            </nut-scroller>


        </nut-tab-panel>

        <nut-tab-panel tab-title="">
            <nut-scroller
                    :type="'vertical'"
                    :is-un-more="isUnMore"
                    :is-loading="isLoading"
                    @pulldown="pulldown"
                    unloadMoreTxt=""
                    @loadMore="loadMoreVert"
                    :init-data="msgList"

            >

                <div slot="list">
                    <li class="mli" v-for="momentItem in msgList" :key="momentItem._id">

                        <div class="mItem">
                            <div class="mleft">

                                <img :src="momentItem.avatar"
                                     alt="">

                            </div>

                            <div class="mRight">
                                <div class="nickname">{{momentItem.nickname}}</div>
                                <span class="content">{{momentItem.content}}</span>

                                <div class="title">
                                    <span class="title-desc"></span>
                                    {{momentItem.title}}</div>


                            </div>
                        </div>

                        <div class="mBottom">
                            <span class="readStatus" :class="momentItem.hasRead==1?'read':''">{{momentItem.hasRead==1?'':''}}</span>
                            <div class="mTime">{{momentItem.createTime}}</div>
                        </div>

                    </li>

                </div>
            </nut-scroller>
        </nut-tab-panel>

        <nut-tab-panel tab-title="">
            <nut-scroller
                    :type="'vertical'"
                    :is-un-more="isUnMore"
                    :is-loading="isLoading"
                    @pulldown="pulldown"
                    unloadMoreTxt=""
                    @loadMore="loadMoreVert"
                    :init-data="msgList"

            >

                <div slot="list">
                    <li class="mli" v-for="wendaItem in msgList" :key="wendaItem._id">

                        <div class="mItem">
                            <div class="mleft">

                                <img :src="wendaItem.avatar"
                                     alt="">

                            </div>

                            <div class="mRight">
                                <div class="nickname">{{wendaItem.nickname}}</div>

                                <div class="title">
                                    <span class="title-desc"></span>
                                    {{wendaItem.title}}
                                    <span class="title-desc"></span>
                                </div>


                            </div>
                        </div>

                        <div class="mBottom">
                            <span class="readStatus" :class="wendaItem.hasRead==1?'read':''">{{wendaItem.hasRead==1?'':''}}</span>
                            <div class="mTime">{{wendaItem.createTime}}</div>
                        </div>

                    </li>

                </div>
            </nut-scroller>
        </nut-tab-panel>

        <nut-tab-panel tab-title="">
            <nut-scroller
                    :type="'vertical'"
                    :is-un-more="isUnMore"
                    :is-loading="isLoading"
                    @pulldown="pulldown"
                    unloadMoreTxt=""
                    @loadMore="loadMoreVert"
                    :init-data="msgList"

            >

                <div slot="list">
                    <li class="mli" v-for="sysItem in msgList" :key="sysItem._id">

                        <div class="mItem">

                            <div class="mRight">
                                <div>{{sysItem.title}}</div>

                                <!--a-->
                                <div class="title" v-html="sysItem.content" @click.prevent="">
                                </div>


                            </div>
                        </div>

                        <div class="mBottom">
                            <div class="mTime">{{sysItem.publishTime}}</div>
                        </div>

                    </li>

                </div>
            </nut-scroller>
        </nut-tab-panel>
    </nut-tab>


</template>

<script>
    import api from '../../api/index'

    export default {
        name: "Message",
        data() {
            return {
                currentTab: 0, //tab
                tabHeight: 600,//tab
                msgList: [],// 
                isUnMore: false,//
                isLoading: false,//
                currentPage: 1,//
                totalPages: 1,//
            }
        },
        created() {
            this.currentTab = this.$route.query.type?this.$route.query.type* 1:0
            //tab
            this.tabHeight = document.body.scrollHeight * 0.89
        },
        mounted() {
            //tab
            this.tabSwitch(this.currentTab)


        },
        methods: {

            /**
             * apitab
             * @param page 
             * @param isMerge 
             *
             */
            async getMsgData(page = 1, isMerge = false) {
                //
                let result = {}

                //tabapitab
                switch (this.currentTab) {

                    case 0:{
                        //
                        result = await api.getAtMessage(page)
                        break
                    }
                    case 1:{
                        //

                        result = await api.getThumbMessage(page)
                        break
                    }
                    case 2: {
                        //
                        result = await api.getArticleMessage(page)
                        break;
                    }
                    case 3:{
                        //
                        result = await api.getMomentMessage(page)
                        break
                    }
                    case 4:{
                        //
                        result = await api.getWendaMessage(page)
                        break
                    }
                    case 5:{
                        //
                        result = await api.getSystemMessage(page)
                        break
                    }


                }
                //
                let msgList = result.data.content

                //
                this.totalPages = result.data.totalPages

                if (isMerge) {
                    //
                    //
                    let obj = {}
                    for (let i = 0; i < this.msgList.length; i++) {
                        obj[this.msgList[i]._id] = this.msgList[i]
                    }
                    for (let i = 0; i < msgList.length; i++) {
                        //id obj  this.msgList

                        if (!obj[msgList[i].id]) {
                            this.msgList.push(msgList[i])
                        } else {
                            // console.log('')
                        }
                    }
                } else {
                    //
                    this.msgList = msgList
                }

                this.isUnMore = false
                this.isLoading = false
            },


            /**
             * 
             */
            pulldown() {

                this.isLoading = true

                //tab
                this.getMsgData()


            },


            /**
             * 
             */
            loadMoreVert() {
                //
                if (this.currentPage >= this.totalPages) {
                    // 
                    // console.log("")
                    this.isUnMore = true
                    return
                }

                this.isUnMore = true


                //tab
                this.getMsgData(++this.currentPage, true)


            },

            /**
             * tab
             */
            tabSwitch(val) {
                //tab
                this.currentTab = val;

                //
                this.getMsgData()

            },


        }
    }
</script>

<style scoped>
    .tab {
        height: 100%;
    }

    .nut-tab {
        height: 80%;
    }

    .nut-tab-title-leftnav .nut-tab-active a{
        color: #1d7dfa!important;
    }
    .mli {
        width: 271px;
        margin-top: 20px;
        padding-bottom: 20px;
        border-bottom: 1px solid #ccc;
    }

    .mItem {
        display: flex;
        font-size: 18px;
    }

    .mleft img {

        width: 40px;
        height: 40px;
        border-radius: 50%;
    }

    .mRight {
        padding-left: 10px;
        display: flex;
        flex-direction: column;

    }

    .mRight .nickname {
        margin-bottom: 10px;
        font-weight: bold;
        font-size: 16px;

    }

    .mRight .content {
        font-size: 16px;
        line-height: 1.5;
    }

    .mRight .title {

        margin-top: 10px;
        font-size: 14px;
        color: #406599;
        line-height: 18px;
    }
    .mRight .title .title-desc{
        color: #000;
    }
    .mRight .thumbUpInfo{
        font-size: 16px;
    }


    .mBottom {

        margin-top: 16px;
        display: flex;
        justify-content: space-between;
        font-size: 15px;
    }

    .mBottom .readStatus {
        padding: 2px 5px 2px 5px;
        color: #fff;
        font-size: 12px;
        border-radius: 10%;
        background-color: #f56c6c;

        height: 20px;
        text-align: center;
        line-height: 20px;
    }

    .mBottom .read {
        background-color: #e6a23c;
    }

    .mType {

        width: 46px;
        height: 22px;
        text-align: center;
        line-height: 22px;
        border: 1px solid #ccc;
        font-size: 13px;
        color: #636363;
        border-radius: 10%;
    }

    .mTime {
        height: 22px;
        line-height: 22px;
        align-self: end;
    }


    .nut-scroller {
        height: calc(100vh - 100px);


    }





</style>

<style>
    .nut-tab-title-leftnav .nut-tab-active a{
        color: #1d7dfa!important;
    }
</style>
