<template>
    <div>

        <!---->
        <nut-navbar
                @on-click-back="back"
                :leftShow="true"
                :right-show="false"
        >
            
        </nut-navbar>
        <!---->
        <h1 class="title">SOB</h1>
        <!---->
        <div class="loginform">

            <form @submit.prevent="login">

                <nut-textinput
                        v-model="loginFrom.phone"
                        placeholder=""
                        maxlength="11"
                        :clearBtn="true"
                        type="text"
                        name="username"
                        autocomplete="username"
                />

                <nut-textinput
                        placeholder=""
                        v-model="loginFrom.password"
                        type="password" name="password" autocomplete="current-password"
                />

                <nut-row>
                    <nut-col :span="15">
                        <nut-textinput
                                class="captchaInput"
                                v-model="loginFrom.captcha"
                                placeholder=""
                                :clearBtn="true"
                                :disabled="false"
                        />
                    </nut-col>
                    <nut-col :span="9">
                        <img class="captchaImg" id="captcha" @click="loadCaptcha" alt="">
                    </nut-col>
                </nut-row>

                <div class="loginbtn">
                    <!---->

                    <button type="submit"></button>
                </div>


            </form>


        </div>
        <!---->
        <div class="other">
            <span></span>
            <span class="regText"></span>
        </div>


        <!--    -->
        <div class="copyright">

            <span>Copyright  2014-2021  code by TrillGates</span>
            <span class="about"> |  | </span>

        </div>


    </div>
</template>

<script>

    import api from '../../api'

    import {mapState} from 'vuex'

    export default {
        name: "Login",
        data() {
            return {
                captchaUrl: 'https://api.sunofbeaches.com/uc/ut/captcha?code=',
                randomCode: '',
                loginFrom: {
                    phone: '', //
                    password: '',//
                    captcha: ''//
                }
            }
        },
        computed: {
            ...mapState(['lastUrl'])
        },
        mounted() {
            this.loadCaptcha()
        },
        methods: {
            /**
             * 
             */
            async login() {
                let {phone, password, captcha} = this.loginFrom

                //
                if (!(/^1[3456789]\d{9}$/.test(phone))) {
                    this.$notify.warn('');
                    return false;
                }
                //
                if (!password) {
                    this.$notify.warn('');
                    return false;
                }

                //
                if (!captcha) {
                    this.$notify.warn('');
                    return false;
                }

                //

                let result = await api.login(phone, password, captcha)
                if (!result.success) {

                    //
                    this.$notify.danger(result.message);

                    //
                    this.randomCode = parseInt(Math.random() * (10000 - 1 + 1) + 1, 10);
                    this.loginFrom.captcha = ''
                } else {
                    this.$notify.success(result.message)


                    //vuex
                    this.$store.dispatch('setUserInfo', this)

                    //
                    this.$router.replace(this.lastUrl)

                }


            },

            /**
             * 
             */
            back() {
                this.$router.back()
            },


            /**
             * 
             */
            loadCaptcha() {
                this.randomCode = parseInt(Math.random() * (10000 - 1 + 1) + 1, 10);

                fetch(this.captchaUrl + this.randomCode).then((response) => {
                    //key
                    let l_c_i = response.headers.get("l_c_i");
                    localStorage.setItem("l_c_i", l_c_i);

                    response.blob().then(function (myBlob) {
                        const urlCreator = window.URL || window.webkitURL;
                        document.getElementById("captcha").src = urlCreator.createObjectURL(myBlob);
                    });
                });
            }
        }
    }
</script>

<style scoped>
    .title {
        margin-top: 35px;
        text-align: center;
        font-size: 67px;
        color: #37f;
    }

    .loginform {
        padding: 20px 20px 0;
    }

    .nut-textinput {
        margin-top: 20px;
    }


    .captchaInput {

    }

    .captchaImg {

        margin: 17px 0 0 15px;


    }

    .loginbtn {
        text-align: center;
        margin-top: 20px;
    }

    .loginbtn button {
        width: 100%;
        height: 32px;
        background-color: #409eff;
        color: #fff;
        border: none;
        border-radius: 5px;
    }

    .other {
        display: flex;
        justify-content: space-between;

        font-size: 17px;
        padding: 20px 20px 0;
        color: #606bc2;
    }

    .copyright {
        margin-top: 252px;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        text-align: center;
        font-size: 12px;
        color: #666;
    }

    .copyright .about {
        margin-top: 10px;
    }


</style>
