                    >
                    </skeleton-square>

                    <column>
                        <skeleton-square
                                width="300px"
                                :count="2"
                                margin="10px 10px 5px"
                        ></skeleton-square>
                    </column>


                </row>

                <row slot="list" padding="15px 10px 0">

                    <skeleton-square
                            width="50px"
                            height="50px"
                    >
                    </skeleton-square>

                    <column>
                        <skeleton-square
                                width="300px"
                                :count="2"
                                margin="10px 10px 5px"
                        ></skeleton-square>
                    </column>


                </row>
                <row slot="list" padding="15px 10px 0">

                    <skeleton-square
                            width="50px"
                            height="50px"
                    >
                    </skeleton-square>

                    <column>
                        <skeleton-square
                                width="300px"
                                :count="2"
                                margin="10px 10px 5px"
                        ></skeleton-square>
                    </column>


                </row>
                <row slot="list" padding="15px 10px 0">

                    <skeleton-square
                            width="50px"
                            height="50px"
                    >
                    </skeleton-square>

                    <column>
                        <skeleton-square
                                width="300px"
                                :count="2"
                                margin="10px 10px 5px"
                        ></skeleton-square>
                    </column>


                </row>
                <row slot="list" padding="15px 10px 0">

                    <skeleton-square
                            width="50px"
                            height="50px"
                    >
                    </skeleton-square>

                    <column>
                        <skeleton-square
                                width="300px"
                                :count="2"
                                margin="10px 10px 5px"
                        ></skeleton-square>
                    </column>


                </row>
                <row slot="list" padding="15px 10px 0">

                    <skeleton-square
                            width="50px"
                            height="50px"
                    >
                    </skeleton-square>

                    <column>
                        <skeleton-square
                                width="300px"
                                :count="2"
                                margin="10px 10px 5px"
                        ></skeleton-square>
                    </column>


                </row>
                <row slot="list" padding="15px 10px 0">

                    <skeleton-square
                            width="50px"
                            height="50px"
                    >
                    </skeleton-square>

                    <column>
                        <skeleton-square
                                width="300px"
                                :count="2"
                                margin="10px 10px 5px"
                        ></skeleton-square>
                    </column>


                </row>
                <row slot="list" padding="15px 10px 0">

                    <skeleton-square
                            width="50px"
                            height="50px"
                    >
                    </skeleton-square>

                    <column>
                        <skeleton-square
                                width="300px"
                                :count="2"
                                margin="10px 10px 5px"
                        ></skeleton-square>
                    </column>


                </row>
            </div>


        </div>


        <template  v-else>
            <!---->
            <nut-scroller class="tagScroll">

                <div slot="list" class="tagItem nut-hor-list-item " :class="{tagCurrent: currentTag==-1}"
                     @click="changeTag(-1,null)"
                >
                    <span class="tagTitle"></span>
                </div>

                <div slot="list" class="tagItem nut-hor-list-item " :class="{tagCurrent: currentTag==index}"
                     v-for="(item,index) in categoryList" :key="item.id"
                     @click="changeTag(index,item)"
                >
                    <span class="tagTitle">{{item.categoryName}}</span>
                </div>

            </nut-scroller>
            <div class="line"></div>
            <nut-scroller

                    ref="mynutscroller"

                    :scrollTo="scrollY"
                    type="vertical"
                    class="totalScroll"
                    @loadMore="loadMoreVert"
                    @pulldown="pulldown"
                    @scrollChange="scrollChange"
                    :is-un-more="isUnMore"
                    :is-loading="isLoading"
                    @scrollToCbk="scrollToCbk"
                    unloadMoreTxt=""
            >
                <div slot="list" class="nut-hor-list-item">



                    <!---->
                    <nut-swiper
                            class="img-swiper"
                            v-show="currentTag==-1"
                            :autoPlay="2000"
                            direction="horizontal"
                            :loop="true"
                            :canDragging="false"
                            :paginationVisible="true"
                            :swiperData="loopList"
                            ref="demo3"
                    >
                        <div class="nut-swiper-slide" v-for="item in loopList" :key="item.id">
                            <img :src="item.picUrl" class="swiperImg" alt="">
                        </div>
                    </nut-swiper>


                    <!-- -->
                    <div id="totalScroll">

                        <ul class="articleList">
                            <!---->
                            <li class="articleItem" v-for="item in recommendArticleList" :key="item.id"
                                @click="toArticleDetail(item.id)">
                                <div class="left">
                                    <img style="object-fit: cover;" :src="item.covers[0]" alt="">
                                </div>
                                <div class="right">
                                    <h2 class="title">{{item.title}}</h2>
                                    <div class="articleInfo">
                                        <span class="author articleInfoItem">{{item.nickName}}</span>
                                        <span class="viewCount articleInfoItem">{{item.viewCount}}</span>
                                        <span class="thumbUp articleInfoItem">{{item.thumbUp}}</span>

                                    </div>
                                </div>


                            </li>


                        </ul>


                    </div>

                </div>


            </nut-scroller>
