</template>

<script>
    import api from '../../api'
    import {nanoid} from 'nanoid'

    export default {
        name: "Home.vue",
        data() {
            return {
                isSkeletonLoading: true,//
                categoryList: [], //
                currentTag: -1,//tag
                currentTagId: '',//tagid
                loopList: [],//
                recommendArticleList: [], //
                currentPage: 0, //
                isUnMore: false,//
                isLoading: false,//
                isShowBacktop: false, //
                scrollY: 0, //
                currentScroll: 0,//
                scrollElem: {},//
            }
        },
        methods: {
            /**
             * 
             */
            async getCategoryList() {
                let result = await api.getCategoryList()
                this.categoryList = result.data
            },

            /**
             * 
             * @returns {Promise<void>}
             */
            async getLoopList() {
                let result = await api.getLoopList()

                for (let i = 0; i < result.data.length; i++) {
                    result.data[i].id = nanoid()
                }

                this.loopList = result.data

            },
            /**
             * 
             * @returns {Promise<void>}
             */
            async getArticleList(page = 1, isMerge = false) {
                let result = null
                //categoryId
                if (this.currentTagId) {
                    result = await api.getArticleListByCategoryId(this.currentTagId, page)
                } else {

                    result = await api.getRecommendArticleList(page)

                }
                if (isMerge) {
                    //
                    //
                    // let obj = {}
                    // for (let i = 0; i < this.recommendArticleList.length; i++) {
                    //     obj[this.recommendArticleList[i].id] = this.recommendArticleList[i]
                    // }
                    // for (let i = 0; i < result.data.list.length; i++) {
                    //     //id obj  this.recommendArticleList
                    //
                    //     if (!obj[result.data.list[i].id]) {
                    //         this.recommendArticleList.push(result.data.list[i])
                    //     } else {
                    //         // console.log(result.data.list[i].name + '')
                    //     }
                    // }
                    this.recommendArticleList = this.mergeTargetFromSource(this.recommendArticleList,result.data.list,'id')
                } else {
                    //
                    this.recommendArticleList = result.data.list
                }

                //
                this.isLoading = false
                this.isUnMore = false
                this.$nextTick(() => {
                    this.isSkeletonLoading = false

                })

            },

            /**
             * 
             */
            loadMoreVert() {
                this.isUnMore = true
                this.getArticleList(++this.currentPage, true)



            },

            /**
             * 
             */
            pulldown() {
                this.isLoading = true
                this.getArticleList(0, false)


            },

            /**
             * 
             */
            scrollChange(scrollY) {

                this.currentScroll = scrollY
                if ((-scrollY) > 200) {
                    this.isShowBacktop = true
                } else {
                    this.isShowBacktop = false

                }
            },

            /**
             * 
             */
            backToTop() {

                //
               /* clearInterval(this.scrollTimer)
                //
                this.scrollTimer = setInterval(() => {
                    //
                    if (!(this.currentScroll >= 0)) {

                        //
                        //1010
                        let moveDistance = Math.ceil((-this.currentScroll * 0.3))

                        //
                        if (moveDistance < 30) {
                            this.scrollY = -1
                            clearInterval(this.scrollTimer) //
                            return
                        }

                        // +
                        this.currentScroll += moveDistance
                        this.scrollY = this.currentScroll
                    } else {
                        //
                        clearInterval(this.scrollTimer)
                    }
                }, 50)*/


                this.scrollElem.style.transform = 'translate3d(0px, -1px, 0px)'


                //nutui ,nutui
                setTimeout(()=>{
                    //scrollY nutui
                    if (this.scrollY===-2){
                        this.scrollY = -.02
                    }else {
                        this.scrollY=  -2
                    }

                },500)
            },

            /**
             * 
             * 
             */
            scrollToCbk() {
                console.log("scrollToCbk")
            },

            /**
             * 
             * @param tagIndex
             */
            changeTag(tagIndex, tagItem) {
                this.currentTag = tagIndex


                //tagItem
                if (tagItem) {
                    this.currentTagId = tagItem.id


                } else {

                    //currentTagIdnull
                    this.currentTagId = null


                }

                this.getArticleList(1, false)
            },

            /**
             * 
             * @param id
             */
            toArticleDetail(id) {
                this.$router.push({
                    path: '/detail',
                    query: {id: id}
                })
            },


        },
        mounted() {
            this.isSkeletonLoading = true
            //
            this.getCategoryList()

            //
            this.getLoopList()

            //
            this.getArticleList(1)


            let _this = this
            //
            setTimeout(()=>{
                _this.scrollElem = document.getElementsByClassName('nut-vert-list')[0]



            },500)



        }
    }
</script>

<style scoped>

    /*--------------- ---------------*/
    .vue-skeleton-loading {
        position: fixed;
        top: 5px;
        right: 0;
        bottom: 0;
        left: -10px;
        z-index: 9998;
        font-size: 12px;
        background: #fff;
    }
    .vue-skeleton-loading .skeleton-bac-animation {
        position: absolute;
        z-index: auto;
        width: 100%;
        height: 100%;
        background: -webkit-gradient(linear,left top,right top,from(hsla(0,0%,100%,0)),color-stop(50%,hsla(0,0%,100%,.5)),color-stop(80%,hsla(0,0%,100%,0)));
        background: -o-linear-gradient(left,hsla(0,0%,100%,0),hsla(0,0%,100%,.5) 50%,hsla(0,0%,100%,0) 80%);
        background: linear-gradient(90deg,hsla(0,0%,100%,0),hsla(0,0%,100%,.5) 50%,hsla(0,0%,100%,0) 80%);
        background-size: 30% 100%;
        background-repeat: no-repeat;
        -webkit-animation: backpos .9s ease-in-out 0s infinite;
        animation: backpos .9s ease-in-out 0s infinite;
    }

    .totalScroll {
        height: calc(100vh - 59px);
    }

    /*--------------- start---------------*/
    .tagScroll {
        white-space: nowrap;
        height: 40px;
    }

    .tagItem {
        box-sizing: border-box;

        height: 40px;
        line-height: 40px;
        text-align: center;

        font-weight: bold;
        font-size: 15px;
        margin: 0 10px;
        color: #8a919f;
    }

    /*tag*/
    .tagCurrent {
        border-bottom: 2px solid #1e80ff;
        margin-bottom: 1px;
        color: #1e80ff;

    }

    /*--------------- end---------------*/


    /*--------------- start---------------*/
    .myswiper {
        height: 168px;
        width: 100%;
    }

    .line {
        background-color: #f4f5f5;
        width: 100%;
        height: 10px;
    }
    .swiperImg {
        width: 100%;
        height: 168px;
    }

    .nut-swiper {
        height: 168px;
    }

    /*--------------- end---------------*/

    /*--------------- start---------------*/
    .articleItem {
        box-sizing: border-box;
        padding: 13px 17px 8px;
        min-height: 83px;

        width: 100%;
        border-bottom: 1px solid #e4e4e4;
        display: flex;
    }

    .articleItem .title {
        line-height: 130%;
        font-size: 20px;
        font-weight: 500;


    }

    .articleInfo {
        display: flex;
        margin-top: 10px;
        font-size: 13px;
    }

    .articleInfoItem {
        margin-right: 10px;
    }


    .articleItem .left img {
        overflow: hidden;
        border-radius: 5px;
        height: 60px;
        width: 60px;
        margin-right: 20px;
    }


    /*--------------- end---------------*/


</style>
